.toolbar {
	width: 100%;
	position: fixed;
	background: #521751;
	height: 56px;
}

.toolbar__navigation {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	height: 100%;
	padding: 0 1rem;
}

.toolbar__logo a {
	color: #fff;
	text-decoration: none;
	font-size: 1.5rem;
	margin-left: 10px;
}


.toolbar_navigation-items a {
	color: white;
	text-decoration: none;
}

.spacer {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.toolbar_navigation-items ul {
	list-style: none;
	margin: 0;
	padding: 0;
	display: -ms-flexbox;
	display: flex;
}

.toolbar_navigation-items li {
	padding: 0 0.5rem;
}

.toolbar_navigation-items a:hover, 
.toolbar_navigation-items a:active {
	color: #fa923f;	
}



